'use client';

import { useState, useRef, useEffect } from "react";
import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";
import CourseCard from "@/components/CourseCard";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Star, PlayCircle, CheckCircle, ChevronDown, Play, Pause, Volume2, VolumeX, Lock } from "lucide-react";
import { toast } from "sonner";
import { Course, quiz } from "./sections/CourseSection";
import { calculateDiscountedPrice, getFile, parseOptions } from "@/lib/utils";
import CourseHero from "./course-hero";

 

export const CourseDetails = ({ slug }: { slug: string }) => {
    const [openSections, setOpenSections] = useState<string[]>([]);
    const [selectedAnswers, setSelectedAnswers] = useState<Record<string, string>>({});
    const [quizResults, setQuizResults] = useState<Record<string, boolean>>({});
    const [playingVideo, setPlayingVideo] = useState<string | null>(null);
    const [completedLessons, setCompletedLessons] = useState<string[]>(["lesson-1-1"]);
    const [openQuizLesson, setOpenQuizLesson] = useState<string | null>(null);
    const [course, setCourse] = useState<Course | null>(null);


    useEffect(() => {
        if (!slug) return;

        const loadCourse = async () => {
            try {
                const res = await fetch(`${process.env.NEXT_PUBLIC_BASE_URL}/api/courses/${slug}`);
                const data = await res.json();
                console.log(data)
                setCourse(data);
            } catch (err) {
                console.error(err);
            }
        };

        loadCourse();
    }, [slug]);






    const toggleSection = (sectionId: string) => {
        setOpenSections(prev =>
            prev.includes(sectionId)
                ? prev.filter(id => id !== sectionId)
                : [...prev, sectionId]
        );
    };

    const handleQuizSubmit = (lessonId: string, quiz?: quiz) => {
        const userAnswer = selectedAnswers[lessonId];

    };

    const curriculum = [
        {
            id: "section-1",
            title: "Introduction to Web Development",
            lessons: [
                {
                    id: "lesson-1-1",
                    title: "What is Web Development?",
                    duration: "10:30",
                    description: "Learn about the fundamentals of web development and what you'll build in this course.",
                    videoUrl: "https://www.w3schools.com/html/mov_bbb.mp4",
                    quiz: {
                        question: "What are the three core technologies of web development?",
                        options: [
                            { id: "a", text: "HTML, CSS, JavaScript" },
                            { id: "b", text: "Python, Java, C++" },
                            { id: "c", text: "React, Angular, Vue" },
                            { id: "d", text: "MySQL, MongoDB, PostgreSQL" }
                        ],
                        correctAnswer: "a"
                    }
                },
                {
                    id: "lesson-1-2",
                    title: "Setting Up Your Development Environment",
                    duration: "15:45",
                    description: "Install and configure the necessary tools for web development including VS Code, Node.js, and browser dev tools.",
                    videoUrl: "https://www.w3schools.com/html/movie.mp4",
                    quiz: null
                }
            ]
        },
        {
            id: "section-2",
            title: "HTML5 Fundamentals",
            lessons: [
                {
                    id: "lesson-2-1",
                    title: "HTML Document Structure",
                    duration: "12:20",
                    description: "Understand the basic structure of an HTML document and essential tags.",
                    videoUrl: "https://www.w3schools.com/html/mov_bbb.mp4",
                    quiz: {
                        question: "Which tag is used for the largest heading?",
                        options: [
                            { id: "a", text: "<head>" },
                            { id: "b", text: "<h6>" },
                            { id: "c", text: "<h1>" },
                            { id: "d", text: "<header>" }
                        ],
                        correctAnswer: "c"
                    }
                },
                {
                    id: "lesson-2-2",
                    title: "Working with Forms",
                    duration: "18:30",
                    description: "Create interactive forms with various input types and validation.",
                    videoUrl: "https://www.w3schools.com/html/movie.mp4",
                    quiz: null
                }
            ]
        },
        {
            id: "section-3",
            title: "CSS3 & Responsive Design",
            lessons: [
                {
                    id: "lesson-3-1",
                    title: "CSS Selectors and Properties",
                    duration: "14:15",
                    description: "Master CSS selectors, properties, and values to style your web pages.",
                    videoUrl: "https://www.w3schools.com/html/mov_bbb.mp4",
                    quiz: null
                }
            ]
        }
    ];

 

    const getPreviousLessonId = (sectionIndex: number, lessonIndex: number) => {
        if (sectionIndex === 0 && lessonIndex === 0) return null;
        let prevSectionIndex = sectionIndex;
        let prevLessonIndex = lessonIndex - 1;

        if (prevLessonIndex < 0) {
            prevSectionIndex = sectionIndex - 1;
            prevLessonIndex = curriculum[prevSectionIndex].lessons.length - 1;
        }

        return curriculum[prevSectionIndex].lessons[prevLessonIndex]?.id;
    };

    const isLessonLocked = (sectionIndex: number, lessonIndex: number) => {
        const prevLessonId = getPreviousLessonId(sectionIndex, lessonIndex);
        if (!prevLessonId) return false;
        return !completedLessons.includes(prevLessonId);
    };

    const markLessonCompleted = (lessonId: string) => {
        setCompletedLessons((prev) => (prev.includes(lessonId) ? prev : [...prev, lessonId]));
        toast.success("Lesson marked as completed");
    };

    return (
        <div className="min-h-screen flex flex-col">
            <Navigation />

            <main className="flex-1">
                {/* Course Hero */}
                <CourseHero course={course}/>

                {/* Course Content */}
                <section className="py-12">
                    <div className="container mx-auto px-4">
                        <Tabs defaultValue="overview" className="w-full">
                            <TabsList className="grid w-full max-w-md grid-cols-3">
                                <TabsTrigger value="overview">Overview</TabsTrigger>
                                <TabsTrigger value="curriculum">Curriculum</TabsTrigger>
                                <TabsTrigger value="reviews">Reviews</TabsTrigger>
                            </TabsList>

                            <TabsContent value="overview" className="mt-8">
                                <Card>
                                    <CardContent className="p-6">
                                        <h2 className="text-2xl font-bold text-foreground mb-4">What you&apos;ll learn</h2>
                                        <div className="grid md:grid-cols-2 gap-3 mb-8">
                                            {[
                                                "Build professional websites from scratch",
                                                "Master HTML5, CSS3, and JavaScript",
                                                "Create responsive designs for all devices",
                                                "Work with React and modern frameworks",
                                                "Build full-stack applications with Node.js",
                                                "Deploy applications to production",
                                            ].map((item, index) => (
                                                <div key={index} className="flex items-start gap-2">
                                                    <CheckCircle className="h-5 w-5 text-primary mt-0.5 flex-shrink-0" />
                                                    <span className="text-muted-foreground">{item}</span>
                                                </div>
                                            ))}
                                        </div>

                                        <h2 className="text-2xl font-bold text-foreground mb-4">Requirements</h2>
                                        <ul className="list-disc list-inside text-muted-foreground space-y-2 mb-8">
                                            <li>A computer with internet connection</li>
                                            <li>No prior programming experience required</li>
                                            <li>Willingness to learn and practice</li>
                                        </ul>

                                        <h2 className="text-2xl font-bold text-foreground mb-4">Description</h2>
                                        <p className="text-muted-foreground mb-4">
                                            Welcome to the Complete Web Development Bootcamp! This comprehensive course will take you from complete beginner to professional web developer. You&apos;ll learn everything you need to know to build modern, responsive websites and web applications.
                                        </p>
                                        <p className="text-muted-foreground">
                                            Throughout the course, you&apos;ll work on real-world projects and build a professional portfolio that will help you land your dream job. Join thousands of students who have already transformed their careers with this course.
                                        </p>
                                    </CardContent>
                                </Card>
                            </TabsContent>

                            <TabsContent value="curriculum" className="mt-8">
                                <Card>
                                    <CardContent className="p-6">
                                        <h2 className="text-2xl font-bold text-foreground mb-6">Course Curriculum</h2>
                                        <div className="space-y-4">
                                            {course?.lessions?.map((lesion, sectionIndex) => (
                                                <Collapsible
                                                    key={lesion.id}
                                                    open={openSections.includes(`${lesion.id}`)}
                                                    onOpenChange={() => toggleSection(`${lesion.id}`)}
                                                >
                                                    <Card>
                                                        <CardContent className="p-0">
                                                            <CollapsibleTrigger className="w-full p-4 hover:bg-muted/50 transition-colors">
                                                                <div className="flex justify-between items-center">
                                                                    <div className="text-left">
                                                                        <h3 className="font-semibold text-foreground mb-1">
                                                                            Section {sectionIndex + 1}: {lesion.title}
                                                                        </h3>
                                                                        <p className="text-sm text-muted-foreground">
                                                                            {course?.lessions?.length} lessons
                                                                        </p>
                                                                    </div>
                                                                    <ChevronDown
                                                                        className={`h-5 w-5 transition-transform ${openSections.includes(`${lesion.id}`) ? "rotate-180" : ""
                                                                            }`}
                                                                    />
                                                                </div>
                                                            </CollapsibleTrigger>

                                                            <CollapsibleContent>
                                                                <div className="border-t">
                                                                    {course?.lessions?.map((lesson, lessonIndex) => (
                                                                        <Collapsible key={lesson.id}>
                                                                            <div className="border-b last:border-b-0">
                                                                                <CollapsibleTrigger className="w-full p-4 hover:bg-muted/30 transition-colors">
                                                                                    <div className="flex items-center justify-between">
                                                                                        <div className="flex items-center gap-3 text-left">
                                                                                            <PlayCircle className="h-5 w-5 text-primary flex-shrink-0" />
                                                                                            <div>
                                                                                                <p className="font-medium text-foreground">
                                                                                                    {lessonIndex + 1}. {lesson.title}
                                                                                                </p>

                                                                                            </div>
                                                                                        </div>
                                                                                        <ChevronDown className="h-4 w-4 text-muted-foreground" />
                                                                                    </div>
                                                                                </CollapsibleTrigger>

                                                                                <CollapsibleContent>
                                                                                    <div className="p-4 space-y-4 bg-muted/20">
                                                                                        <p className="text-sm text-muted-foreground">
                                                                                            {lesson.description}
                                                                                        </p>

                                                                                        {/* Video Player */}
                                                                                        {isLessonLocked(sectionIndex, lessonIndex) ? (
                                                                                            <div className="relative bg-black rounded-lg overflow-hidden aspect-video flex items-center justify-center">
                                                                                                <div className="text-center px-4">
                                                                                                    <Lock className="mx-auto mb-3 h-8 w-8 text-muted-foreground" />
                                                                                                    <p className="text-foreground font-semibold mb-1">Lesson locked</p>
                                                                                                    <p className="text-sm text-muted-foreground">
                                                                                                        Complete the previous lesson to unlock this video.
                                                                                                    </p>
                                                                                                </div>
                                                                                            </div>
                                                                                        ) : (
                                                                                            <AdvancedLessonVideo
                                                                                                src={getFile(lesson?.files?.[0]?.file_path || "")}
                                                                                                lessonId={`${lesson.id}`}
                                                                                                title={lesson.title}
                                                                                                onComplete={markLessonCompleted}
                                                                                                playingVideo={playingVideo}
                                                                                                setPlayingVideo={setPlayingVideo}
                                                                                            />
                                                                                        )}

                                                                                        {/* Quiz Section */}
                                                                                        {lesson.quiz && (
                                                                                            <div className="mt-4">
                                                                                                <Button
                                                                                                    variant="outline"
                                                                                                    className="w-full justify-between"
                                                                                                    onClick={() => setOpenQuizLesson(`${lesson.id}`)}
                                                                                                >
                                                                                                    <span>Open lesson quiz</span>
                                                                                                    <ChevronDown className="h-4 w-4" />
                                                                                                </Button>

                                                                                                <Dialog
                                                                                                    open={openQuizLesson === `${lesson.id}`}
                                                                                                    onOpenChange={(open) => {
                                                                                                        if (!open) setOpenQuizLesson(null);
                                                                                                    }}
                                                                                                >
                                                                                                    <DialogContent className="max-w-lg">
                                                                                                        <DialogHeader>
                                                                                                            <DialogTitle>Lesson Quiz</DialogTitle>
                                                                                                        </DialogHeader>
                                                                                                        <div className="mt-2">
                                                                                                            {lesson.quiz?.questions?.map((question, qIndex) => (
                                                                                                                <div key={qIndex}>
                                                                                                                    <p className="text-foreground mb-4">{question.options}</p>
                                                                                                                    <RadioGroup
                                                                                                                        value={selectedAnswers[lesson.id]}
                                                                                                                        onValueChange={(value) =>
                                                                                                                            setSelectedAnswers({ ...selectedAnswers, [lesson.id]: value })
                                                                                                                        }
                                                                                                                        className="space-y-3"
                                                                                                                    >
                                                                                                                        {parseOptions(question.options).map((option, index) => (
                                                                                                                            <div key={index} className="flex items-center space-x-2">
                                                                                                                                <RadioGroupItem value={option} id={`${lesson.id}-${index}`} />
                                                                                                                                <Label
                                                                                                                                    htmlFor={`${lesson.id}-${index}`}
                                                                                                                                    className="cursor-pointer text-muted-foreground"
                                                                                                                                >
                                                                                                                                    {option}
                                                                                                                                </Label>
                                                                                                                            </div>
                                                                                                                        ))}
                                                                                                                    </RadioGroup>
                                                                                                                </div>
                                                                                                            ))}
                                                                                                            <Button
                                                                                                                className="mt-4 w-full"
                                                                                                                onClick={() => handleQuizSubmit(`${lesson.id}`, lesson.quiz)}
                                                                                                                disabled={!selectedAnswers[lesson.id]}
                                                                                                            >
                                                                                                                Submit Answer
                                                                                                            </Button>

                                                                                                            {quizResults[lesson.id] !== undefined && (
                                                                                                                <div
                                                                                                                    className={`mt-3 p-3 rounded-lg ${quizResults[lesson.id]
                                                                                                                        ? "bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400"
                                                                                                                        : "bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-400"
                                                                                                                        }`}
                                                                                                                >
                                                                                                                    <p className="text-sm font-medium">
                                                                                                                        {quizResults[lesson.id]
                                                                                                                            ? " Correct! Great job!"
                                                                                                                            : " Incorrect. Review the lesson and try again."}
                                                                                                                    </p>
                                                                                                                </div>
                                                                                                            )}
                                                                                                        </div>
                                                                                                    </DialogContent>
                                                                                                </Dialog>
                                                                                            </div>
                                                                                        )}
                                                                                    </div>
                                                                                </CollapsibleContent>
                                                                            </div>
                                                                        </Collapsible>
                                                                    ))}
                                                                </div>
                                                            </CollapsibleContent>
                                                        </CardContent>
                                                    </Card>
                                                </Collapsible>
                                            ))}
                                        </div>
                                    </CardContent>
                                </Card>
                            </TabsContent>

                            <TabsContent value="reviews" className="mt-8">
                                <Card>
                                    <CardContent className="p-6">
                                        <h2 className="text-2xl font-bold text-foreground mb-6">Student Reviews</h2>
                                        <div className="space-y-6">
                                            {[
                                                { name: "John Doe", rating: 5, date: "2 weeks ago", comment: "Excellent course! Very comprehensive and well-structured. I learned so much!" },
                                                { name: "Jane Smith", rating: 5, date: "1 month ago", comment: "Best web development course I've taken. The instructor explains everything clearly." },
                                                { name: "Mike Johnson", rating: 4, date: "2 months ago", comment: "Great content and projects. Would recommend to anyone starting web development." },
                                            ].map((review, index) => (
                                                <Card key={index}>
                                                    <CardContent className="p-4">
                                                        <div className="flex items-start gap-3">
                                                            <img
                                                                src={`https://images.unsplash.com/photo-${1500000000000 + index}?w=50&h=50&fit=crop`}
                                                                alt={review.name}
                                                                className="w-10 h-10 rounded-full object-cover"
                                                            />
                                                            <div className="flex-1">
                                                                <div className="flex items-center justify-between mb-1">
                                                                    <h3 className="font-semibold text-foreground">{review.name}</h3>
                                                                    <span className="text-sm text-muted-foreground">{review.date}</span>
                                                                </div>
                                                                <div className="flex items-center gap-1 mb-2">
                                                                    {[...Array(review.rating)].map((_, i) => (
                                                                        <Star key={i} className="h-4 w-4 fill-yellow-400 text-yellow-400" />
                                                                    ))}
                                                                </div>
                                                                <p className="text-muted-foreground">{review.comment}</p>
                                                            </div>
                                                        </div>
                                                    </CardContent>
                                                </Card>
                                            ))}
                                        </div>
                                    </CardContent>
                                </Card>
                            </TabsContent>
                        </Tabs>
                    </div>
                </section>

                {/* Related Courses */}
                <section className="py-12 bg-muted/30">
                    <div className="container mx-auto px-4">
                        <h2 className="text-2xl font-bold text-foreground mb-6">Related Courses</h2>
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
                            {/* Placeholder related courses */}
                        </div>
                    </div>
                </section>
            </main>

            <Footer />
        </div>
    );
};

export default CourseDetails;

type AdvancedLessonVideoProps = {
    src: string;
    lessonId: string;
    title: string;
    onComplete: (lessonId: string) => void;
    playingVideo: string | null;
    setPlayingVideo: (id: string | null) => void;
};

const AdvancedLessonVideo = ({
    src,
    lessonId,
    title,
    onComplete,
    playingVideo,
    setPlayingVideo,
}: AdvancedLessonVideoProps) => {
    const videoRef = useRef<HTMLVideoElement | null>(null);
    const [duration, setDuration] = useState(0);
    const [currentTime, setCurrentTime] = useState(0);
    const [volume, setVolume] = useState(1);
    const [isMuted, setIsMuted] = useState(false);
    const [playbackRate, setPlaybackRate] = useState(1);

    const isPlaying = playingVideo === lessonId;

    const togglePlay = () => {
        const el = videoRef.current;
        if (!el) return;
        if (isPlaying) {
            el.pause();
            setPlayingVideo(null);
        } else {
            el.play();
            setPlayingVideo(lessonId);
        }
    };

    const handleTimeUpdate = () => {
        const el = videoRef.current;
        if (!el) return;
        setCurrentTime(el.currentTime);
        setDuration(el.duration || 0);
    };

    const handleSeek = (value: number[]) => {
        const el = videoRef.current;
        if (!el) return;
        const nextTime = value[0];
        el.currentTime = nextTime;
        setCurrentTime(nextTime);
    };

    const handleVolumeChange = (value: number[]) => {
        const el = videoRef.current;
        if (!el) return;
        const nextVolume = value[0];
        el.volume = nextVolume;
        setVolume(nextVolume);
        setIsMuted(nextVolume === 0);
    };

    const toggleMute = () => {
        const el = videoRef.current;
        if (!el) return;
        const nextMuted = !isMuted;
        el.muted = nextMuted;
        setIsMuted(nextMuted);
    };

    const handleRateChange = (rate: number) => {
        const el = videoRef.current;
        if (!el) return;
        el.playbackRate = rate;
        setPlaybackRate(rate);
    };

    const handleEnded = () => {
        setPlayingVideo(null);
        onComplete(lessonId);
    };

    const formatTime = (time: number) => {
        if (!Number.isFinite(time)) return "0:00";
        const minutes = Math.floor(time / 60);
        const seconds = Math.floor(time % 60)
            .toString()
            .padStart(2, "0");
        return `${minutes}:${seconds}`;
    };

    const progress = duration ? (currentTime / duration) * 100 : 0;

    return (
        <div className="relative bg-black rounded-lg overflow-hidden">
            <video
                ref={videoRef}
                className="w-full aspect-video"
                controls={false}
                onTimeUpdate={handleTimeUpdate}
                onLoadedMetadata={handleTimeUpdate}
                onEnded={handleEnded}
            >
                <source src={src} type="video/mp4" />
                Your browser does not support the video tag.
            </video>
            <div className="absolute inset-x-0 bottom-0 bg-gradient-to-t from-black/80 to-transparent p-4 space-y-2">
                <div className="text-xs text-white/80 mb-1 truncate">{title}</div>
                <div className="flex items-center gap-3 text-white">
                    <Button
                        variant="ghost"
                        size="icon"
                        className="h-8 w-8 text-white hover:bg-white/20"
                        onClick={togglePlay}
                    >
                        {isPlaying ? <Pause className="h-4 w-4" /> : <Play className="h-4 w-4" />}
                    </Button>
                    <div className="flex-1 flex items-center gap-3">
                        <div className="flex items-center gap-2 text-xs min-w-[72px]">
                            <span>{formatTime(currentTime)}</span>
                            <span className="text-white/60">/</span>
                            <span>{formatTime(duration)}</span>
                        </div>
                        <div className="flex-1">
                            <Slider
                                value={[currentTime]}
                                max={duration || 0}
                                step={0.1}
                                onValueChange={handleSeek}
                            />
                        </div>
                    </div>
                    <div className="flex items-center gap-2">
                        <Button
                            variant="ghost"
                            size="icon"
                            className="h-8 w-8 text-white hover:bg-white/20"
                            onClick={toggleMute}
                        >
                            {isMuted || volume === 0 ? (
                                <VolumeX className="h-4 w-4" />
                            ) : (
                                <Volume2 className="h-4 w-4" />
                            )}
                        </Button>
                        <div className="w-20">
                            <Slider
                                value={[volume]}
                                max={1}
                                step={0.05}
                                onValueChange={handleVolumeChange}
                            />
                        </div>
                        <select
                            className="bg-black/40 text-xs text-white px-2 py-1 rounded-md border border-white/10"
                            value={playbackRate}
                            onChange={(e) => handleRateChange(Number(e.target.value))}
                        >
                            {[0.5, 1, 1.5, 2].map((rate) => (
                                <option key={rate} value={rate}>
                                    {rate}x
                                </option>
                            ))}
                        </select>
                    </div>
                </div>
            </div>
        </div>
    );
};
